import React from "react";
import resumeFile from "../documents/resume.pdf";

const Resume = ({ classicHeader, darkTheme }) => {
  const educationDetails = [
    {
      yearRange: "2020-2024",
      title: "B.Sc in Physics with Medical Physics and Bioengineering",
      place: "Technological University Dublin",
      desc: "Studies and labs encompassed an in-depth study of advanced physics principles, specialized medical technology applications, and bioengineering innovations. Key skills acquired include critical thinking, advanced mathematical analysis, and an in-depth understanding of modern medical technologies.",
    },
    {
      yearRange: "2014-2020",
      title: "Secondary Education",
      place: "St. Aidan’s Christian Brother School (CBS)",
      desc: "Key accomplishments include development of analytical reasoning and foundational knowledge in biological and physical sciences.",
    },
  ];

  const experienceDetails = [
    {
      yearRange: "January 2023 - August 2023",
      title: "Physics & Engineering Intern",
      place: "Children’s Health Ireland",
      desc: "Engaged in the application of physics principles in medical settings, focusing on equipment testing and quality assurance. Key achievements include gaining hands-on experience with diagnostic technologies and contributing to maintaining high standards of medical equipment safety.",
    },
    {
      yearRange: "April 2020 - Present",
      title: "Financial Coordinator (Volunteer)",
      place: "Betel Youth",
      desc: "Managed financial operations and event planning, demonstrating proficiency in financial oversight, budgeting, and organizational logistics. These roles highlighted skills in fiscal management, strategic planning, and event coordination.",
    },
    {
      yearRange: "August 2018 – December 2018",
      title: "Administrative Intern",
      place: "Nial Corr, Private Solicitor Company",
      desc: "Focused on legal administrative tasks, developing a keen eye for detail, procedural adherence, and organizational efficiency. Acquired skills in legal documentation, office management, and collaborative team engagement.",
    },
  ];

  const skills = [
    {
      name: "Biomedical Equipment",
      percent: 90,
    },
    {
      name: "Interdisciplinary Collaboration",
      percent: 90,
    },
    {
      name: "Medical Device Design Principles",
      percent: 85,
    },
    {
      name: "Python Programming for Data Analysis",
      percent: 80,
    },
    {
      name: "Healthcare Technology Compliance",
      percent: 95,
    },
    {
      name: "Scientific Presentation Skills",
      percent: 85,
    },
  ];

  return (
    <section
      id="resume"
      className={"section " + (darkTheme ? "bg-dark-1" : "")}
    >
      <div className={"container " + (classicHeader ? "" : "px-lg-5")}>
        {/* Heading */}
        <div className="position-relative d-flex text-center mb-5">
          <h2
            className={
              "text-24  text-uppercase fw-600 w-100 mb-0 " +
              (darkTheme ? "text-muted opacity-1" : "text-light opacity-4")
            }
          >
            Summary
          </h2>
          <p
            className={
              "text-9 text-dark fw-600 position-absolute w-100 align-self-center lh-base mb-0 " +
              (darkTheme ? "text-white" : "text-dark")
            }
          >
            {" "}
            Resume
            <span className="heading-separator-line border-bottom border-3 border-primary d-block mx-auto" />
          </p>
        </div>
        {/* Heading end*/}
        <div className="row gx-5">
          {/* My Education */}
          <div className="col-md-6">
            <h2
              className={
                "text-6 fw-600 mb-4 " + (darkTheme ? "text-white" : "")
              }
            >
              My Education
            </h2>
            {educationDetails.length > 0 &&
              educationDetails.map((value, index) => (
                <div
                  key={index}
                  className={
                    "bg-white  rounded p-4 mb-4 " +
                    (darkTheme ? "bg-dark" : "bg-white border")
                  }
                >
                  <p className="badge bg-primary text-2 fw-400">
                    {value.yearRange}
                  </p>
                  <h3 className={"text-5 " + (darkTheme ? "text-white" : "")}>
                    {value.title}
                  </h3>
                  <p className={darkTheme ? "text-primary" : "text-danger"}>
                    {value.place}
                  </p>
                  <p className={"mb-0 " + (darkTheme ? "text-white-50" : "")}>
                    {value.desc}
                  </p>
                </div>
              ))}
          </div>
          {/* My Experience */}
          <div className="col-md-6">
            <h2
              className={
                "text-6 fw-600 mb-4 " + (darkTheme ? "text-white" : "")
              }
            >
              My Experience
            </h2>
            {experienceDetails.length > 0 &&
              experienceDetails.map((value, index) => (
                <div
                  key={index}
                  className={
                    "bg-white  rounded p-4 mb-4 " +
                    (darkTheme ? "bg-dark" : "bg-white border")
                  }
                >
                  <p className="badge bg-primary text-2 fw-400">
                    {value.yearRange}
                  </p>
                  <h3 className={"text-5 " + (darkTheme ? "text-white" : "")}>
                    {value.title}
                  </h3>
                  <p className={darkTheme ? "text-primary" : "text-danger"}>
                    {value.place}
                  </p>
                  <p className={"mb-0 " + (darkTheme ? "text-white-50" : "")}>
                    {value.desc}
                  </p>
                </div>
              ))}
          </div>
        </div>
        {/* My Skills */}
        <h2
          className={
            "text-6 fw-600 mt-4 mb-4 " + (darkTheme ? "text-white" : "")
          }
        >
          My Skills
        </h2>
        <div className="row gx-5">
          {skills.length > 0 &&
            skills.map((skill, index) => (
              <div key={index} className="col-md-6">
                <p
                  className={
                    " fw-500 text-start mb-2 " +
                    (darkTheme ? "text-light" : "text-dark")
                  }
                >
                  {skill.name}{" "}
                  <span className="float-end">{skill.percent}%</span>
                </p>
                <div
                  className={
                    "progress progress-sm mb-4 " + (darkTheme ? "bg-dark" : "")
                  }
                >
                  <div
                    className="progress-bar"
                    role="progressbar"
                    style={{ width: skill.percent + "%" }}
                    aria-valuenow={skill.percent}
                    aria-valuemin={0}
                    aria-valuemax={100}
                  />
                </div>
              </div>
            ))}
        </div>
        <div className="text-center mt-5">
          <a
            className="btn btn-outline-secondary rounded-pill shadow-none"
            href="CV.pdf"
            target="_blank"
          >
            Download CV
            <span className="ms-1">
              <i className="fas fa-download" />
            </span>
          </a>
        </div>
      </div>
    </section>
  );
};

export default Resume;
